.education-container {
    margin: 20px 0;
    background-color: #f7f9fc;
    padding: 15px;
    border-radius: 10px;
}

.education-entry, .course-entry {
    margin-bottom: 20px;
}

h3, h4 {
    font-weight: bold;
    margin-top: 0;
}
.education-container h2 {
    color: #333;
    border-bottom: 2px solid #333;
    padding-bottom: 10px;
    margin-bottom: 20px;
}
p {
    margin: 5px 0;
    font-size: 1em;
    line-height: 1.5em;
}
.image-container {
    text-align: left; /* Alinea el contenido a la izquierda */
}

.aws-badge {
    max-width: 150px; /* Ajusta esto al tamaño deseado */
    height: auto;
    display: inline-block; /* Cambiado de block a inline-block para permitir la alineación */
    margin-right: auto; /* Mantiene la imagen alineada a la izquierda */
}

.aws-badge-responsive {
    width: 100%; /* Ajusta al ancho del contenedor */
    height: auto; /* Mantiene la relación de aspecto */
    max-width: 150px; /* El máximo ancho que puede tener */
    display: inline-block; /* Permite la alineación con text-align */
}
.badge-container {
    text-align: left; /* Alinea el contenido a la izquierda */
}

.badge-responsive {
    width: 100%; /* Ajusta al ancho del contenedor */
    height: auto; /* Mantiene la relación de aspecto */
    max-width: 150px; /* El máximo ancho que puede tener */
    display: inline-block; /* Permite la alineación con text-align */
}
.badge {
    max-width: 150px; /* Ajusta esto al tamaño deseado */
    height: auto;
    display: inline-block; /* Cambiado de block a inline-block para permitir la alineación */
    margin-right: auto; /* Mantiene la imagen alineada a la izquierda */
}
